.home-section-4 {
  padding: 100px 40px 100px;
  // background-color: #000;
  align-items: center;
  position: relative;
}
.home-section-4-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}
.section-4-frame {
  position: relative;
  width: 80vw;
  height: 500px;
  // border-radius: 2px;
  // box-shadow: 1px 2px 10px 0px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  background: #333;
  color: #fff;
}
.section-4-boxes {
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  width: 100%;
  height: 100%;

  .box {
    position: relative;
    width: 20%;
    transition: all 1s ease-in-out;
    cursor: pointer;
    overflow: hidden;
  }
  .section-4-img-1 {
    background: url(../../../../assets/gallery-1.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-2 {
    background: url(../../../../assets/gallery-2.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-3 {
    background: url(../../../../assets/gallery-3.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-4 {
    background: url(../../../../assets/gallery-4.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-5 {
    background: url(../../../../assets/gallery-5.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-6 {
    background: url(../../../../assets/gallery-6.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-7 {
    background: url(../../../../assets/markantalya.jpg);
    background-size: cover;
    background-position: center;
  }
}
.hoverfunc:hover {
  width: 100%;
  cursor: url(../../../../assets/plus.svg), nesw-resize;
}
.home-section-4-i:hover {
  transform: scale(1.01);
}

////////////////
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 1074px;
  // box-shadow: 0 4rem 6rem rgba(0, 0, 0, 0.3);
  z-index: 11;
  transition: all 0.5s;
  display: flex;
  gap: 40px;
  align-items: center;

  .image-container {
    width: 1074px;
    height: 600px;
    // background-color: red;
    // background-image: url("../../../assets//clinic-9.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.797);
  backdrop-filter: blur(4px);
  z-index: 10;
  transition: all 0.5s;
}

.btn--close-modal {
  font-family: inherit;
  color: inherit;
  position: absolute;
  top: -4%;
  right: 6%;
  font-size: 4rem;
  width: 60px;
  cursor: pointer;
  border: none;
  background: none;
  color: #fff;
}
.modal-btn-left {
  width: 300px;
  height: 300px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    transform: rotate(180deg);
  }
}
.modal-btn-right {
  width: 300px;
  height: 300px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-num {
  color: #fff;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
  bottom: 0%;
  left: 48%;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}
